import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useLocation, useHistory } from 'react-router-dom';
import { mainList } from './main-list';
import { backendUrl } from '../../config/config';

import Spinner from '../../components/Spinner/index';

import './mobile-main-menu.scss';

const MobileMainMenu = () => {
  const location = useLocation();
  const history = useHistory();
  const path = location.pathname.slice(0, 3);
  const [loading, setLoading] = useState(false);
  const [categories, setCategories] = useState([]);

  useEffect(() => {
    const onSetUpScreen = async () => {
      setLoading(true);
      const result = await axios.get(backendUrl + '/products/categories');
      const cats = result.data.cats;
      cats.splice(0, 1);
      setLoading(false);
      setCategories(cats);
    };

    onSetUpScreen();
  }, [location]);

  return (
    <div className='mobile-main-menu'>
      {loading && <Spinner />}
      {!loading && (
        <>
          {categories?.map((el, index) => (
            <div className='item' key={index} onClick={() => history.push(path + '/' + el.route)}>
              <img src={backendUrl + '/' + el.seo_image} />
              <p className='label'>{el.name_ru}</p>
            </div>
          ))}
        </>
      )}
    </div>
  );
};

export default MobileMainMenu;
